<template>
    <div id="app">
        <form class="box">
            <label for="username">Username</label>
            <p class="control">
                <input id="username" class="input" name="username" v-model="user.name">
            </p>

            <label for="password">Password</label>
            <p class="control">
                <vue-password-custom v-model="user.password"
                                     classes="input"
                                     name="vue-password-custom"
                                     id="vue-password-custom"
                                     :score="parseInt(score, 10)"
                                     :user-inputs="[user.name]"
                                     minlength="8"
                                     v-on:input="getStrengthScore"
                                     :strengthMessages="['Must. Do. Better ðŸ’©','Yawn ðŸ˜«','Is that all you\'ve got? ðŸ˜†','Strong ðŸ’ª','That\'s bananas! ðŸŒ']"
                >
                </vue-password-custom>
                <span class="help" v-if="user.password.length < 8">Password must be at least 8 characters</span>
                <span class="help" v-if="pwned > 0 && user.password.length > 0">This password matches {{pwned.toLocaleString()}} hacked accounts according to <a href="https://haveibeenpwned.com/" target="_blank">haveibeenpwned.com</a></span>
            </p>

            <p>&nbsp;</p>

            <label for="password-confirm">Confirm Password</label>
            <p class="control">
                <input id="password-confirm"
                       class="input"
                       type="password"
                       name="password-confirm"
                       v-model="user.confirmPassword"
                >
                <span class="help" v-if="!passwordsMatch && user.confirmPassword.length > 0">Passwords do not match</span>
            </p>

            <p>&nbsp;</p>

            <p class="control">
                <button class="button is-primary"
                        :disabled="score < 3 || pwned > 0 || user.password.length < 8 || user.name.length < 1 || !passwordsMatch"
                >Register</button>
            </p>
        </form>
    </div>
</template>

<script>
    import VuePasswordCustom from 'vue-password/dist/custom'
    import zxcvbn from 'zxcvbn'
    import crypto from 'crypto'
    import axios from 'axios'

    export default {
        name: 'app',
        components: {
            VuePasswordCustom
        },
        data () {
            return {
                user: {
                    name: '',
                    password: '',
                    confirmPassword: ''
                },
                score: 0,
                pwned: 0
            }
        },
        computed: {
            passwordsMatch() {
                return this.user.password === this.user.confirmPassword;
            }
        },
        methods: {
            getStrengthScore: function(password, userInputs) {

                let url = "https://api.pwnedpasswords.com/range/"

                let pwHash = crypto.createHash('sha1').update(password).digest('hex').toUpperCase();
                let prefix = pwHash.slice(0,5);
                let suffix = pwHash.slice(5);

                let path = url + prefix;

                axios.get(path)
                    .then(response =>  {
                        response = response.data;

                        this.score = response.includes(suffix) ? 0 : zxcvbn(password, userInputs).score;
                        this.pwned = response.includes(suffix) ? +response.split('\r\n').find(r => r.includes(suffix)).split(':').pop() : 0;

                    })
                    .catch(error => {
                        throw error;
                    });
            }
        }
    }
</script>

<style>
    body {
        max-width: 600px;
        margin: 10rem auto;
    }
</style>

